p{
	text-align: center;	
	font-size: 1.33em;
}


.header
{
	grid-area: "head";

	grid-column-start: 1;
    grid-column-end: 4;

	grid-row-start: 1;
	grid-row-end: 2;

	height: 15vh;

	background-color: beige;
}

.navigation-menu
{
	grid-area: "navi";

	grid-column-start: 1;
	grid-column-end: 4;

	grid-row-start: 2;
	grid-row-end: 3;

	height: 10vh;
	background-color: lime;
}

.main-content
{
	grid-area: "main";

	grid-column-start: 2;
	grid-column-end: 4;

	grid-row-start: 3;
	grid-row-end: 4;

	height: 60vh;
	background-color: yellow;	
}

.left-main-content
{
	grid-area: "lMai";

	grid-column-start: 1;
	grid-column-end: 2;

	grid-row-start: 3;
	grid-row-end: 4;

	height: 60vh;
	background-color: teal;
}

